import { Button, ColorPicker, Slider, Switch } from 'antd';
import React, { useEffect, useState } from 'react';
import "./boxshadowngenerator.css";

function BoxShadowGenerator(props) {

    const [horizontal, setHorizontal] = useState(10)
    const [vertical, setVertical] = useState(10)
    const [blur, setBlur] = useState(10)
    const [spread, setSpread] = useState(10)
    const [inset, setInset] = useState(false)
    const [shadowColor, setShadowColor] = useState("#000000")
    const [boxColor, setBoxColor] = useState("rgb(125, 55, 255)")
    const [isCopied, setIsCopied] = useState(false)

    const handleCopy = () => {
        var CSSs = document.querySelectorAll("#text_css p");
        console.log(CSSs);
        var copyText = [...CSSs].map(x => x.innerText).join("\n");
        console.log(copyText);
        navigator.clipboard.writeText(copyText);
        setIsCopied(true);
    }

    useEffect(() => {
        setIsCopied(false)
    }, [horizontal, vertical, blur, spread, inset, shadowColor])

    return (
        <div className='boxshadow'>
            <div className='option'>

                <div className='title'><h2>Box Shadow Options</h2></div>

                {/* Horizontal Shadow Length */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Horizontal Shadow Length</span>
                        <span className='option_title_value'>{horizontal}px</span>
                    </div>
                    <Slider defaultValue={horizontal} max={200} min={-200} onChange={setHorizontal}/>
                </div>

                {/* Vertical Shadow Length */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Vertical Shadow Length</span>
                        <span className='option_title_value'>{vertical}px</span>
                    </div>
                    <Slider defaultValue={vertical} max={200} min={-200} onChange={setVertical}/>
                </div>

                {/* Blur Radius */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Blur Radius</span>
                        <span className='option_title_value'>{blur}px</span>
                    </div>
                    <Slider defaultValue={blur} max={400} min={0} onChange={setBlur}/>
                </div>

                {/* Spread Radius */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Spread Radius</span>
                        <span className='option_title_value'>{spread}px</span>
                    </div>
                    <Slider defaultValue={spread} max={200} min={-200} onChange={setSpread}/>
                </div>

                {/* Shadow Color */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Shadow Color</span>
                    </div>
                    <div className='option_body'>
                        <ColorPicker defaultValue={shadowColor} showText={(color) => {
                        setShadowColor(color.toHexString());
                        return <span style={{display: "block" , width: "60px"}}>{color.toHexString()}</span>
                    }} />
                    </div>
                </div>

                {/* Box Color */}
                <div className = "option_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Box Color</span>
                    </div>
                    <div className='option_body'>
                        <ColorPicker defaultValue={boxColor} showText={(color) => {
                        setBoxColor(color.toHexString());
                        return <span style={{display: "block" , width: "60px"}}>{color.toHexString()}</span>
                    }}/>
                    </div>
                </div>

                {/* Inset */}
                <div className = "option_row last_row">
                    <div className='option_title'>
                        <span className='option_title_name'>Inset</span>
                    </div>
                    <div className='option_body'>
                        <Switch checkedChildren="On" unCheckedChildren="Off" onChange={setInset} />
                    </div>
                </div>
            </div>
            <div className='result'>
                <div className='result_box_container'>
                    <div className='result_box' style={{boxShadow: `${horizontal}px ${vertical}px ${blur}px ${spread}px ${shadowColor} ${inset ? 'inset' : ''}`,
                                WebkitBoxShadow: `${horizontal}px ${vertical}px ${blur}px ${spread}px ${shadowColor} ${inset ? 'inset' : ''}`,
                                MozBoxShadow: `${horizontal}px ${vertical}px ${blur}px ${spread}px ${shadowColor} ${inset ? 'inset' : ''}`,
                                backgroundColor: `${boxColor}`}}></div>
                </div>
                <div className='result_text'>
                    <div id='text_css'>
                        <p>box-shadow: {horizontal}px {vertical}px {blur}px {spread}px {shadowColor}{inset ? ' inset' : ''};</p>
                        <p>-webkit-box-shadow: {horizontal}px {vertical}px {blur}px {spread}px {shadowColor}{inset ? ' inset' : ''};</p>
                        <p>-moz-box-shadow: {horizontal}px {vertical}px {blur}px {spread}px {shadowColor}{inset ? ' inset' : ''};</p>
                    </div>
                    <Button className='result_text_copy' type="primary" onClick={handleCopy}>{isCopied ? 'Copied' : 'Copy'}</Button>
                </div>
            </div>
        </div>
    );
}

export default BoxShadowGenerator;